<template>
	<div class="main">
		<head>
			<link href="https://fonts.googleapis.com/css?family=EB+Garamond&display=swap" rel="stylesheet">
		</head>

		<div class="left-div">
			<div class="over-left-div" v-on:click="closeContact"></div>

			<div class="mul-circle circle-grey c1"></div>
			<div class="mul-circle circle-blue c2"></div>
			<div class="mul-circle circle-green c3"></div>
			<div class="mul-circle circle-green c4"></div>
			<div class="mul-circle circle-grey c5"></div>
			<div class="mul-circle circle-green c6"></div>
			<div class="mul-circle circle-blue c6"></div>
			<div class="mul-circle circle-green c7"></div>
			<div class="mul-circle circle-blue c8"></div>
			<div class="mul-circle circle-green c9"></div>
			<div class="mul-circle circle-green c10"></div>
			<div class="mul-circle circle-grey c11"></div>
			<div class="mul-circle circle-green c12"></div>
			<div class="mul-circle circle-blue c13"></div>
			<div class="mul-circle circle-blue c14"></div>
			<div class="mul-circle circle-green c15"></div>
			<div class="mul-circle circle-grey c16"></div>
			<div class="mul-circle circle-green c17"></div>
			<div class="mul-circle circle-green c18"></div>
			<div class="mul-circle circle-blue c19"></div>

			<div class="vasePNG"> <img class="vasePNG" src="../assets/vase.png"> </div>

			<router-link to="/about-me"> <div class="aboutMe">
				<div class="aboutMe-text"> About me </div>
			</div> </router-link>

			<div class="contact" v-on:click="contactOnclick()" :style="{width: contactWidth + 'px', height: contactHeight + 'px'}" width="100px" height="200px">
				<div class="contact-text"  v-if="showContact == 0"> Contact </div>

				<div class="contact-show" v-if="showContact == 1"> 
					<a href="" onclick="window.open(this.href); return false;">
						<img src="../assets/fb.png" alt="Facebook" class="circle-logo">
					</a>
					<a href="" onclick="window.open(this.href); return false;">
						<img src="../assets/linkedin.png" alt="Linkedin" class="circle-logo">
					</a>
					<router-link to="/contact">
						<img src="../assets/email.png" alt="Email" class="circle-logo">
					</router-link>
				</div>
			</div>

		</div>

		<div class="mid-title">
			<router-link to="/portefolio">
				<div class="shape-1"> </div>
				<div class="portefolio">
						Portefolio
				</div>
			</router-link>
			<div class="name">
				Marlene L.
			</div>

		</div>

		<div class="right-div">
			<div class="bg"> </div>
		</div>
	</div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
		showContact: 0,
		contactWidth: 100,
		contactHeight: 200,
		msg: 'Welcome to Your Vue.js App'
    }
  },
  methods: {
	  contactOnclick: function(){
		if (this.showContact == 0) {
			this.showContact = 1;
			this.contactWidth = 300;
			this.contactHeight = 150;
		}
		else {
			this.showContact = 0;
			this.contactWidth = 100;
			this.contactHeight = 200;
		}

	  },
	  closeContact: function() {
		this.showContact = 0;
		this.contactWidth = 100;
		this.contactHeight = 200;
	  }
  },
}
</script>

<style scoped>

.main {
	z-index: 	1;
	position:	fixed;
	top: 		0;
	bottom: 	0;
	left: 		0;
	right: 		0;
}

.left-div {
	z-index: 	2;
	position: 	fixed;
	top: 		0;
	bottom:		0;
	left:		0;
	width:		40%;
	background:	rgb(255, 253, 250);
}

.over-left-div {
	z-index: 	1;
	position: 	fixed;
	top: 		0;
	bottom:		0;
	left:		0;
	width:		40%;
	background:	transparent;
}

.right-div {
	z-index: 	1;
	position: 	fixed;
	top: 		0;
	bottom:		0;
	right:		0;
	width:		60%;	
	overflow: 	hidden;
	animation: 	move-slow 40s linear infinite;
	background:	url("../assets/bg-blue.jpeg");
}

.mid-title {
	z-index: 	2;
	position: 	fixed;
	top: 		38%;
}

.name {
	z-index:		3;
	position: 		fixed;
	left:			58%;
	display: 		inline;
	font-size: 		140px;
	font-weight:	bold;
	font-family: 	'La Belle Aurore', cursive;
	color:			antiquewhite;
	text-shadow: 	grey 1px 1px 1px;
}

.shape-1 {
	z-index:		2;
	position: 		fixed;
	width: 			400px;
	height: 		400px;
	top: 			30%;
	left: 			28%;
	transform: 		rotate(45deg);
	background:		antiquewhite;
	outline:		grey dashed 1px;
	outline-offset: -10px;
	transition-duration: 1s;
	box-shadow:		grey 1px 0.5px 5px;
}

.shape-1:hover {
	transform: 		rotate(-45deg);
	width: 			450px;
	height: 		450px;
	left:			26.5%;
	opacity: 		0.90;
}

.portefolio {
	z-index:		3;
	position:		fixed;
	top:			46%;
	left: 			31%;
	font-family: 	'EB Garamond', serif;
	font-size: 		70px;
	color:			grey;
	text-shadow: 	grey 1px 1px 1px;
	letter-spacing: 5px;
}

.contact {
	z-index: 					2;
	position: 					fixed;
	bottom: 					10%;
	background:					antiquewhite;
	height: 					200px;
	width: 						100px;
	padding-right:  			20px;
	border-top-right-radius: 	10px 10px;
	border-bottom-right-radius: 10px 10px;
	transition: 				1s;

}

.contact-text {
	z-index: 			10;
	position:			fixed;
	width:				100px;
	height:				200px;
	font-family: 		'EB Garamond', serif;
	bottom: 			8%;
	color:				grey;
	writing-mode: 		vertical-rl;
	font-size:			50px;
	padding-top:		15px;
	padding-left:		10px;
	margin: 			auto;
	transition: 		1s;
}

.contact:hover {
	padding-left: 				100px;
	opacity: 					0.85;
}

.contact-show {
	z-index: 		3;	
	position: 		fixed;
	width:			300px;
	height:			100px;
	bottom: 		10%;
	left:			3%;
	transition: 	1s;		
}

a, img {
	transition: 	1s;	
}

.circle-logo {
	width: 			50px;
	height: 		50px;
	position: 		relative;
	padding-right: 	20px;
}

.email-me {
	display:		inline;
	position:		relative;
	left:			100%;
	font-family: 	'EB Garamond', serif;
	font-size: 		50px;
	color: 			grey;
}

.mul-circle {
	z-index:		2;
	position:		fixed;
	left:			-25px;
	width: 			50px;
	height: 		50px;
	border-radius: 	50%;
}

.aboutMe {
	z-index:					3;
	position:					fixed;
	top:						10%;
	width: 						100px;
	height: 					220px;
	background:					antiquewhite;
	border-top-right-radius: 	10px 10px;
	border-bottom-right-radius: 10px 10px;
	transition: 				1s;
	padding-right:  			20px;
}

.aboutMe:hover {
	padding-left: 				100px;
	opacity: 					0.85;
}

.aboutMe-text {
	z-index: 			4;
	position:			fixed;
	font-family: 		'EB Garamond', serif;
	color:				grey;
    writing-mode: 		vertical-rl;
	width: 				100px;
	height: 			220px;
	font-size:			50px;
	padding-right: 		0;
	padding-top:		14px;
	padding-left:		10px;
	transition: 		1s;
}

.circle-grey {
	background:		#454851;
}

.circle-blue {
	background:		#577687;
}

.circle-green {
	background: 	#849c9b;
}

.vasePNG {
	position: fixed;
	left: 11%;
	top: 10%;
	width: 150px;
	height: 200px;
	animation: mbalance 3s infinite;
}

.c1 { transition-duration: 0.3s; }
.c1:hover { width: 70px }

.c2 { top: 50px; transition-duration: 0.3s; }
.c2:hover { width: 70px }

.c3 { top: 100px; transition-duration: 0.3s; }
.c3:hover { width: 70px }

.c4 { top: 150px; transition-duration: 0.3s; }
.c4:hover { width: 70px }

.c5 { top: 200px; transition-duration: 0.3s; }
.c5:hover { width: 70px }

.c6 { top: 250px; transition-duration: 0.3s; }
.c6:hover { width: 70px }

.c7 { top: 300px; transition-duration: 0.3s; }
.c7:hover { width: 70px }

.c8 { top: 350px; transition-duration: 0.3s; }
.c8:hover { width: 70px }

.c9 { top: 400px; transition-duration: 0.3s; }
.c9:hover { width: 70px }

.c10 { top: 450px; transition-duration: 0.3s; }
.c10:hover { width: 70px }

.c11 { top: 500px; transition-duration: 0.3s; }
.c11:hover { width: 70px }

.c12 { top: 550px; transition-duration: 0.3s; }
.c12:hover { width: 70px }

.c13 { top: 600px; transition-duration: 0.3s; }
.c13:hover { width: 70px }

.c14 { top: 650px; transition-duration: 0.3s; }
.c14:hover { width: 70px }

.c15 { top: 700px; transition-duration: 0.3s; }
.c15:hover { width: 70px }

.c16 { top: 750px; transition-duration: 0.3s; }
.c16:hover { width: 70px }

.c17 { top: 800px; transition-duration: 0.3s; }
.c17:hover { width: 70px }

.c18 { top: 850px; transition-duration: 0.3s; }
.c18:hover { width: 70px }

.c19 { top: 900px; transition-duration: 0.3s; }
.c19:hover { width: 70px }


@keyframes move-slow {
	0% {
		background-size: 110%;
		background-size: 120%;
		background-size: 130%;
	}
	50% {
		background-size: 130%;
		background-size: 120%;
		background-size: 110%;
	}
	100% {
		background-size: 110%;
		background-size: 120%;
		background-size: 130%;
	}
}

@keyframes mbalance {
	0%{
		transform: rotate(5deg);
	}
	50% {
		transform: rotate(-5deg);

	}
	100% {
		transform: rotate(5deg);
	}
}

</style>



